.cover-div{
    width: 96%;
    height: 40vh;
    background-color: grey;
    background-image: url('https://img.freepik.com/free-photo/vivid-blurred-colorful-wallpaper-background_58702-3798.jpg');
    background-size: 100%;
    box-shadow: 2px 2px 2px 2px var(--grey);
    padding: 2%;
    display: grid !important;
    place-items: left !important;
}

.profile-div{
    position: relative;
    color: var(--white);
    width: 250px;
    background-image: radial-gradient(blue, green, blue);
    /* background-image: url('https://e0.pxfuel.com/wallpapers/983/800/desktop-wallpaper-cute-profile-pics-smile-t.jpg'); */
    border: 3px solid var(--white);
    object-fit:cover;
    background-repeat: no-repeat;
    height: 250px;
    border-radius: 60%;
    box-shadow: 3px 3px 3px 3px var(--grey);
}

.card-div{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
}

.recent-heading{
    text-align: center;
    color: var(--primary);
    text-shadow: 1px 1px var(--grey);
}

@media screen and (max-width:500px){
    .cover-div{
        place-items:center;
    }
}